.flag-02-icon {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.text46,
.text47 {
  position: relative;
  line-height: 30px;
  font-weight: 600;
}
.text47 {
  font-size: var(--inter-14-medium-size);
  line-height: 20px;
  color: var(--primary-700);
}
.section-header2,
.title1 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.title1 {
  padding: 0 0 0 var(--padding-sm);
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.section-header2 {
  justify-content: space-between;
}
.text48 {
  position: relative;
  line-height: 20px;
  font-weight: 600;
}
.button-group-base3,
.button-group-base4,
.button-group-base5 {
  background-color: var(--gray-50);
  border-right: 1px solid var(--gray-300);
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-base);
  align-items: center;
  justify-content: center;
  z-index: 2;
}
.button-group-base4,
.button-group-base5 {
  background-color: var(--base-white);
  z-index: 1;
}
.button-group-base5 {
  z-index: 0;
}
.button-group1,
.header-and-options,
.options {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.button-group1 {
  border-radius: var(--br-5xs);
  box-shadow: var(--shadow-xs);
  border: 1px solid var(--gray-300);
  overflow: hidden;
  flex-direction: row;
}
.header-and-options,
.options {
  align-self: stretch;
}
.options {
  flex-direction: row;
  font-size: var(--inter-14-medium-size);
  color: var(--gray-800);
}
.header-and-options {
  flex-direction: column;
  gap: var(--gap-sm);
}
.map-wrap {
  flex: 1;
  position: relative;
  height: 381px;
}
.countries {
  align-self: stretch;
  flex: 1;
  flex-direction: column;
  gap: var(--gap-5xl);
}
.content17,
.countries,
.map,
.map-and-data,
.sidebar {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.sidebar {
  width: 248px;
  height: 380px;
  flex-direction: column;
  padding: var(--padding-13xl) var(--padding-xs);
  box-sizing: border-box;
}
.content17,
.map,
.map-and-data {
  align-self: stretch;
}
.map-and-data {
  flex-direction: row;
  gap: var(--gap-5xl);
}
.content17,
.map {
  flex-direction: column;
}
.content17 {
  border-radius: var(--br-xs);
  background-color: var(--base-white);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--gray-200);
  padding: var(--padding-2xs) var(--padding-xs);
}
.map {
  padding: 0 var(--padding-13xl);
  gap: var(--gap-base);
  text-align: left;
  font-size: var(--inter-20-semibold-size);
  color: var(--gray-700);
  font-family: var(--inter-14-medium);
}
