.cards {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.coins-stacked-03-icon {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.text23 {
  position: relative;
  line-height: 30px;
  font-weight: 600;
}
.title {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  padding: 0 0 0 var(--padding-sm);
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  font-size: var(--inter-20-semibold-size);
}
.text24 {
  position: relative;
  line-height: 20px;
  font-weight: 600;
}
.button-group-base,
.button-group-base1,
.button-group-base2 {
  background-color: var(--gray-50);
  border-right: 1px solid var(--gray-300);
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-base);
  align-items: center;
  justify-content: center;
  z-index: 2;
}
.button-group-base1,
.button-group-base2 {
  background-color: var(--base-white);
  z-index: 1;
}
.button-group-base2 {
  z-index: 0;
}
.button-group {
  border-radius: var(--br-5xs);
  box-shadow: var(--shadow-xs);
  border: 1px solid var(--gray-300);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  color: var(--gray-800);
}
.tab-button-base,
.tab-button-base1 {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: var(--padding-12xs) var(--padding-9xs) var(--padding-2xs);
  align-items: center;
  justify-content: center;
}
.tab-button-base1 {
  border-bottom: 2px solid var(--primary-700);
  color: var(--primary-700);
}
.horizontal-tabs,
.section-header,
.y-axis {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
}
.horizontal-tabs {
  border-bottom: 1px solid var(--gray-200);
  flex-direction: row;
  justify-content: flex-start;
  gap: var(--gap-base);
  color: var(--gray-500);
}
.section-header,
.y-axis {
  flex-direction: column;
}
.section-header {
  justify-content: flex-start;
  gap: var(--gap-3xl);
}
.y-axis {
  flex: 1;
  justify-content: space-between;
}
.jan {
  position: relative;
  line-height: 18px;
}
.x-axis1 {
  position: absolute;
  width: calc(100% - 64px);
  top: 0;
  right: 0;
  left: 64px;
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-5xs);
  box-sizing: border-box;
  align-items: center;
  justify-content: space-between;
}
.text32,
.x-axis {
  position: relative;
}
.x-axis {
  align-self: stretch;
  height: 16px;
}
.text32 {
  line-height: 18px;
  font-weight: 500;
}
.x-axis-label {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  padding: var(--padding-5xs) 0 0;
  align-items: flex-start;
  justify-content: center;
  text-align: center;
}
.axis,
.content14 {
  display: flex;
  justify-content: flex-start;
}
.content14 {
  align-self: stretch;
  flex: 1;
  flex-direction: column;
  align-items: flex-end;
}
.axis {
  position: absolute;
  height: calc(100% + 0.29px);
  width: 100%;
  top: 0;
  right: 0;
  bottom: -0.29px;
  left: 0;
  flex-direction: row;
  align-items: flex-start;
}
.line-and-bar-chart {
  align-self: stretch;
  position: relative;
  height: 280.71px;
  text-align: right;
  font-size: var(--inter-12-medium-size);
  color: var(--gray-600);
}
.bar-graph {
  align-self: stretch;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-mini);
  color: var(--gray-700);
}
.cards-and-graph {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-13xl);
}
.all-metrics,
.bar-graph-and-map,
.cards-and-graph {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.bar-graph-and-map {
  flex: 1;
  flex-direction: column;
}
.all-metrics {
  height: 571.71px;
  flex-direction: row;
  padding: 0 var(--padding-13xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--inter-14-medium-size);
  color: var(--gray-600);
  font-family: var(--inter-14-medium);
}
