.text22 {
  position: relative;
  line-height: 38px;
  font-weight: 600;
}
.evening-icon {
  position: relative;
  width: 38px;
  height: 38px;
  overflow: hidden;
  flex-shrink: 0;
}
.text21 {
  display: flex;
  flex-direction: row;
