  gap: var(--gap-xs);
}
.content13 {
  align-self: stretch;
  align-items: flex-start;
  gap: var(--gap-base);
}
.container,
.header-section,
.page-header {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.container {
  padding: 0 var(--padding-13xl) var(--padding-5xs);
}
.header-section {
  text-align: left;
